<template>
  <li class='player' :style="style" :id='id'>
    <ul class='col'>
      <inp
      v-for='n in 6'
      v-bind:id="n"
      v-bind:key="n"
      @input="change"
      ></inp>
      <li class="calc" id='sum1'>{{sum1}}</li>
      <li class="calc" id='bonus'>{{bonus}}</li>
      <inp
      v-for='n in getNumbers(9, 18)'
      v-bind:id="n"
      v-bind:key="n"
      @input="change"
      ></inp>
      <li class="calc" id='sum2'>{{sum2}}</li>
    </ul>
  </li>
</template>

<script>
import inp from './inp.vue'
export default {
  props: ['id', 'num', 'sum2', 'players'],
  data() {
    return {
      vals: {},
      sum1: null,
      bonus: null
    }
  },
  methods: {
    getNumbers:function(start,stop){
      return new Array(stop-start).fill(start).map((n,i)=>n+i);
    },
    change(e) {
      this.vals[e[0]] = e[1];
      this.sum1 = Object.keys(this.vals)
        .filter(key => key < 7)
        .reduce((arr, key) => {
          arr.push(this.vals[key]);
          return arr;
        }, [])
        .reduce((m, x) => m + x, 0);

      if(this.sum1 > 62) {
        this.bonus = 50
      } else {
        this.bonus = null
      }
      var sum2 = 0;
      sum2 = Object.values(this.vals)
        .reduce((m, x) => m + x, 0)
        + this.bonus;
      this.$emit('sum', {id: this.id, sum: sum2})

    }
  },
  components: {
    inp
  },
  computed: {
    style() {
      return 'width: ' + 80 / this.players + 'vw;';
    }
  }
}
</script>


