  width: 1vw;
}

.calc {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
  text-align: center;
  font-family: sans-serif;
  font-size: 9pt;
}

.player:nth-child(even) > .col > li:nth-child(even) {
  background: $bg-light;
}

.player:nth-child(even) > .col > li:nth-child(odd) {
  background: $bg-dark;
}

.player:nth-child(odd) > .col > li:nth-child(even) {
  background: $bg-dark;
}

.player:nth-child(odd) > .col > li:nth-child(odd) {
  background: $bg-light;
}

.col > li {
  height: 4vh;
}
</style>
