<template>
  <div id="app">
    <ul class="main">
      <names></names>
      <player
      v-for="p in players"
      v-bind:id="p"
      v-bind:num="num"
      v-bind:key="p"
      v-bind:sum2="sums[p]"
      v-bind:players="players"
      @sum="sumsChanged"
      ></player>
    </ul>
    <buttons
    @input="playersChanged"
    @click="sum"
    ></buttons>
  </div>
</template>

<script>
import player from './components/player.vue'
import names from './components/names.vue'
import buttons from './components/buttons.vue'


export default {
  name: 'App',
  components: {
    player,
    names,
    buttons
  },
  data() {
    return {
      players: 2,
      num: 22,
      sums: {},
      hiddenSums: {}
    }
  },
  methods: {
    playersChanged(n) {
      this.players = n;
      console.log(this.players);
    },
    sumsChanged(e) {
      this.hiddenSums[e.id] = e.sum;
    },
    sum() {
      this.sums = this.hiddenSums;
    }
  }
}
</script>

