<template>
  <li class="input">
    <input type="number" v-model="num" v-on:input="emitInput()">
  </li>
</template>

<script>
export default {
  props: ['id'],
  data() {
    return {
      num: null
    }
  },
  methods: {
    emitInput() {
      this.$emit('input', [this.id, Number(this.num)]);
    }
  }
}
</script>


<style scoped>
.input {
  display: flex;
  justify-content: center;
  align-items: center;
}

input {
  -webkit-appearance: none;
  border: 0;
  background: none;
  width: 100%;
  border: none;
  display: block;
  height: 100%;
  text-align: center;
  -webkit-appearance: none;
  margin: 0;
  font-family: sans-serif;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

