<template>
  <div class="buttons">
    <input type="number" v-model="players" v-on:input="emitInput()">
    <button v-on:click="emitClick()">sum</button>
  </div>
</template>

<script>
export default {
  data() {
    return {
      players: 1
    }
  },
  methods: {
    emitInput() {
      this.$emit('input', Number(this.players));
    },
    emitClick() {
      this.$emit('click', true);
    }
  }
}
</script>

<style scoped>
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}
</style>
